import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import './styles.css';
import {

} from '../../../constants/actionTypes';
import CreateProduct from './CreateProduct';
import Image from '../../../common/Components/Image';

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {

      product: { },

    };
  }

  componentWillMount() {
    if (this.props.data) {
      this.setState({ product: this.props.data });
    }
  }

  componentWillReceiveProps(np) {
    if (np.data && np.data !== this.state.product) {
      this.setState({ product: np.data });
    }
  }

  render() {
    const {
      name, disc_price, price, images, preview_images,
    } = this.state.product;
    
    return (
      <div className="admin-product relative" style={this.props.style} >
        <div className="product-header-img" style={{ overflow: 'hidden', position: 'relative' }}>
          {
                images.length > 0
                  ? <Image src={images[0]} alt="" overlaySrc={preview_images[0]} />
                  : <img src="" alt="" />
            }
        </div>
        <div className="product-pricing">
          <h3>
            {name}
          </h3>
          {
                disc_price === price
                  ? (
                    <b>
Rs.
                      {price}
                    </b>
                  )

                  : (
                    <div>
                      <b>
Rs.
                        {disc_price}
                      </b>
                      <strike>{` Rs. ${price}`}</strike>

                    </div>
                  )

            }

        </div>

        <CreateProduct data={this.state.product} update />

      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,

});

const mapDispatchToProps = (dispatch) => ({

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
