import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { toast } from 'react-toastify';
import { push } from 'react-router-redux';
import './styles.css';
import _ from 'lodash';
import {
  ADD_TO_CART, REMOVE_FROM_CART,
} from '../../../constants/actionTypes';
import ProductDetails from './ProductDetails';
import { store } from '../../../store';
import CollapseTransition from '../../../common/Components/CollapseTransition';
import Image from '../../../common/Components/Image';

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      user: {},
      product: { },
      quantity: 1,
      images: [],
      alreadyInCart: false,
      cartItem: false,
      isSizesAvailable: false,
      selectedSize: '',
      sizes: [],
    };
    this.cartItems = [];
    this.selectRef = React.createRef();
    this.showCart = this.showCart.bind(this);
  }

  componentWillMount() {
    if (this.props.data) {
      this.setState({ product: this.props.data });
      const { data } = this.props;
      if (data.meta_data && data.meta_data.sizes_available) {
        this.setState({ sizes: data.meta_data.sizes_available, isSizesAvailable: data.meta_data.sizes_available.length > 0 });
      }
    }
    if (this.props.cartItem) {
      this.setState({ cartItem: this.props.cartItem });
    }
    if (this.props.cartItems) {
      this.cartItems = this.props.cartItems;
      const temp = this.props.cartItems.filter((c) => c.item.id === this.props.data.id);
      if (temp.length > 0) {
        this.setState({ alreadyInCart: true, cartSelectedSize: temp[0].selectedSize, cartSelectedQuantity: temp[0].quantity });
      } else {
        this.setState({ alreadyInCart: false, quantity: 1, selectedSize: '' });
      }
    }
  }

  componentWillReceiveProps(np) {
    if (np.data && np.data !== this.state.product) {
      this.setState({ product: np.data });
      const { data } = np;
      if (data.meta_data && data.meta_data.sizes_available) {
        this.setState({ sizes: data.meta_data.sizes_available, isSizesAvailable: data.meta_data.sizes_available.length > 0 });
      }
    }
    if (np.cartItem !== this.state.cartItem) {
      this.setState({ cartItem: np.cartItem });
    }
    if (np.cartItems) {
      this.cartItems = np.cartItems;
      const temp = np.cartItems.filter((c) => c.item.id === np.data.id);

      if (temp.length > 0) {
        this.setState({ alreadyInCart: true, cartSelectedSize: temp[0].selectedSize, cartSelectedQuantity: temp[0].quantity });
      } else {
        this.setState({ alreadyInCart: false, quantity: 1, selectedSize: '' });
      }
    }
  }

  showCart() {
    const { routingLocation } = this.props;
    let path = `${window.location.pathname}?`;
    const { query } = routingLocation;
    query.cartDetails = true;
    Object.keys(query).forEach((q) => {
      path += `${q}=${query[q]}&`;
    });
    path.substring(0, path.length - 2);
    store.dispatch(push(path));
  }

  modifyQuantity(change) {
    if (this.state.quantity + change > 0) this.setState({ quantity: this.state.quantity + change });
  }

  addToCart() {
    const {
      isSizesAvailable, product, quantity, selectedSize,
    } = this.state;
    if (isSizesAvailable && selectedSize === '') {
      this.setState({ showSizes: true });
    } else {
      const data = {
        item: product,
        quantity,
        selectedSize,
      };
      const sizeSelect = this.selectRef.current;
      
      sizeSelect.children[0].selected = true;
      
      this.props.addToCart(data);
      this.setState({ showSizes: false, selectedSize: '' });
      toast.info('Item added to cart !!!');
    }
  }

  removeProduct() {
    const data = {
      item: this.state.product,
      quantity: this.state.cartSelectedQuantity,
      selectedSize: this.state.cartSelectedSize,
    };

    this.props.removeFromCart(data);
  }

  selectSize(e) {
    this.setState({ selectedSize: e.target.value });
  }

  render() {
    const {
      name, disc_price, price, images, preview_images, cod_available,
    } = this.state.product;
    const {
      quantity, alreadyInCart, cartItem, showSizes, sizes,
    } = this.state;
    return (
      <div className="public-product relative" style={this.props.style}>

        <div className="product-header-img relative" style={{ overflow: 'hidden' }}>
          {
            images.length > 0
              ? <Image src={images[0]} alt="" overlaySrc={preview_images[0]} /> : null
          }
          <ProductDetails data={this.state.product} alreadyInCart={alreadyInCart} parent={this.props.parent} cartItem={cartItem} />
        </div>
        <div className="product-pricing">
          <h3 className="ma0 relative">
            <ProductDetails data={this.state.product} alreadyInCart={alreadyInCart} parent={this.props.parent} />
            {name}
          </h3>
          {
            disc_price === price
              ? (
                <b className="green">
                    Rs.
                  {price}
                </b>
              )
              : (
                <div>
                  <div className="home-page-product-sale">
                    {`${Math.floor(((price - disc_price) / price) * 100)}%off`}
                  </div>
                  <b>
                    Rs.
                    {disc_price}
                  </b>
                  <strike>{` Rs. ${price}`}</strike>

                </div>
              )
          }
          {
          cartItem
            ? (
              <div className="product-bottom-div">
                <div className="f6 go-to-cart relative red" onClick={this.removeProduct.bind(this)}>
                  Remove
                </div>
                {
                  cod_available
                    ? <p className="b err-txt green">Cash on delivery is available</p>
                    : <p className="err-txt blue">Cash on delivery not available</p>
                }
              </div>
            )
            : (
              <div className="product-bottom-div">
                <div className="flex">
                  <button type="button" className="w-20" onClick={() => this.modifyQuantity(-1)}> - </button>
                  <input type="number" value={quantity} style={{ fontSize: '1.2rem' }} className="w-60 b" disabled />
                  <button type="button" className="w-20" onClick={() => this.modifyQuantity(1)}>  + </button>
                </div>

                <CollapseTransition visible={showSizes}>
                  <div className="flex flex-column mt1 mb1">
                    <select onChange={this.selectSize.bind(this)} ref={this.selectRef} id="product-size-select">
                      <option selected value="">Select Size</option>

                      {
                        sizes.map((s, i) => (
                          <option value={s} key={i}>{s}</option>
                        ))
                      }
                    </select>
                    {/* <button className="" type="button" onClick={() => this.setState({ showSizes: false })}> Cancel </button> */}
                  </div>
                </CollapseTransition>
                <button className="product-add-to-cart" onClick={this.addToCart.bind(this)}> Add to cart </button>
                {
                  alreadyInCart
                    ? (
                      <div className="f6 go-to-cart relative">
                          Item already in cart.
                        <a onClick={this.showCart}>
                          Go To Cart
                        </a>

                      </div>
                    )
                    : null
                }
              </div>
            )
        }
        </div>


      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  cartItems: state.HomePageReducers.cartItems,
  routingLocation: state.router.location,


});

const mapDispatchToProps = (dispatch) => ({
  addToCart: (product) => dispatch({ type: ADD_TO_CART, payload: product }),
  removeFromCart: (product) => dispatch({ type: REMOVE_FROM_CART, payload: product }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
