import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';

import './ProductsPage.css';

import { toast } from 'react-toastify';
import SideDrawer from '../AdminPage/components/SideDrawer';
import {
  GET_PRODUCTS, GET_SHOP_DETAILS, GET_LINKS, GET_USER_DETAILS, GET_BG_STYLE, GET_HEADER_INFO,
} from '../../constants/actionTypes';
// import { USER_DETAILS } from '../../constants/otherConstants';
import Tabs from '../../common/Components/Tabs';
import Loader from '../../common/Components/Loader';
import HomePage from '../HomePage/HomePage';
import IsEmailVerified from '../IsEmailVerified/IsEmailVerified';
import CopyUrl from '../../common/Components/CopyUrl';
import CollapseTransition from '../../common/Components/CollapseTransition';
import TextInput from '../../common/Components/TextInput';
import CreateProduct from './components/CreateProduct';
import agent from '../../agent';
import DisabledDiv from '../../common/Components/DisabledDiv';
import InfiniteScrollProducts from './components/InfiniteScrollProducts';
import { USER_DETAILS } from '../../constants/otherConstants';
import Switch from '../../common/Components/Switch';

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      mobileView: false,
      user: {},
      isNormalUser: true,
      shippingAddress: 'World Wide Shipping',
      changeShippingAddress: false,
      is_category_view_enabled: false,
    };
    this.products = [];
  }

  componentDidMount() {
    // console.log(this.props);
    window.addEventListener('resize', this.resize.bind(this));
    const username = window.localStorage.getItem(USER_DETAILS);
    const data = { username };
    this.resize();
    const {
      user, getShopDetails, shopDetails, getUserDetails, getBackgroundStyle, getHeaderInfo, getLinks,
    } = this.props;
    if (user && user.user) {
      const temp = user.user.user_type === 'normal';
      this.setState({ user: user.user, isNormalUser: temp });
    }
    if (shopDetails) {
      this.setState({ shippingAddress: shopDetails.address, is_category_view_enabled: shopDetails.is_category_view_enabled });
    }
    getShopDetails(data);
    getUserDetails(data);
    getBackgroundStyle(data);
    getHeaderInfo(data);
    getLinks();
    // getCategories(data);
  }

  componentWillReceiveProps(np) {
    const { user, shippingAddress } = this.state;
    if (np.user) {
      if (user !== np.user.user) {
        const temp = np.user.user.user_type === 'normal';
        this.setState({ user: np.user.user, loading: false, isNormalUser: temp });
      }
    }
    if (np.shopDetails && np.shopDetails.address !== shippingAddress) {
      this.setState({ shippingAddress: np.shopDetails.address, is_category_view_enabled: np.shopDetails.is_category_view_enabled });
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', () => console.log('removed'));
  }


  resize() {
    this.setState({ mobileView: window.innerWidth <= 600 });
  }

  uploadDeliverableArea(e) {
    if (e) {
      e.preventDefault();
    }
    const { shippingAddress } = this.state;
    const data = {
      address: shippingAddress,
    };
    this.setState({ loading: true });
    agent.ProductsPage.setShopDetails(data).then((res) => {
      const { setShopDetails, shopDetails } = this.props;
      const data = { ...shopDetails, address: res.data.address };
      setShopDetails(data);
      this.setState({ loading: false, changeShippingAddress: false });
    }).catch((err) => {
      console.log(err);
      this.setState({ loading: false });
      if (err.response) {
        toast.error(err.response.data.error);
      } else {
        toast.error('Error in setting deliverable address!!!');
      }
    });
  }

  togelDefaultCategoryView() {
    const { is_category_view_enabled } = this.state;
  
    const { shopDetails, setShopDetails } = this.props;
    const details = { ...shopDetails, is_category_view_enabled:!is_category_view_enabled };
    this.setState({ loading: true });
    agent.ProductsPage.setShopDetails(details).then((res) => {
      this.setState({ is_category_view_enabled: !is_category_view_enabled });
      this.setState({ loading: false });
      setShopDetails(details);
    }).catch((err) => {
      console.log('err in updaing previe preference', err, err.response);
      if (err.response) {
        toast.error(err.response.data.error);
      } else {
        toast.error('error occurred in updating default View');
      }
      this.setState({ loading: false });
    });
  }

  previewComponent() {
    const { is_category_view_enabled } = this.state;
    return (
      <div className="admin-home-page">
        <div className="flex justify-between items-center bg-white bl br">
          <p className="  center mt0 mb0 b flex block flex-wrap " style={{ overflowY: 'auto' }}>
            <p className="  center mt0 mb0 b block  ">Categories View </p>
          </p>
          <Switch togelswitch={this.togelDefaultCategoryView.bind(this)} val={!is_category_view_enabled} />
          <p className="  center mt0 mb0 b block  ">All Products </p>
        </div>
        <HomePage parent="admin-page" />
      </div>
    );
  }

  editLinkComponent() {
    const {
      shippingAddress, user, changeShippingAddress, is_category_view_enabled,
    } = this.state;
    const { username } = user;

    return (
      <article className=" w-50 ">
        <IsEmailVerified />

        <div className="justify-center  self-center ">
          <CopyUrl />
          <div className="  bg-white bb">
            <div className="flex justify-between items-center">
              <p className="  center mt0 mb0 b flex block flex-wrap " style={{ overflowY: 'auto' }}>
                <p className="  center mt0 mb0 b block  ">Deliverable areas :</p>
                <p className="  center mt0 mb0 b block  ">{`${shippingAddress}`}</p>
              </p>
              <button className="pointer grow  bg-blue white bl b flex items-center ma2 pa2 br2 " style={{ border: 'none' }} type="button" onClick={() => this.setState({ changeShippingAddress: !changeShippingAddress })}>
              Change Area

              </button>
            </div>
            <CollapseTransition visible={changeShippingAddress}>
              <div>
                <form onSubmit={this.uploadDeliverableArea.bind(this)} className="flex items-center">
                  <TextInput
                    label="Deliverable Areas"
                    value={shippingAddress}
                    className="w-100"
                    type="text"
                    onTextChange={(text) => this.setState({ shippingAddress: text })}
                    required
                  />
                  <button className="pointer grow  bg-blue white bl b flex items-center ma2 pa2 br2 " style={{ border: 'none' }} type="submit">
                  Upload Area

                  </button>
                </form>
              </div>
            </CollapseTransition>

          </div>
          <div className=" center  flex x ">
            <button
              className="   dyna-width tc center  mt2 color-btn relative"
              style={{ padding: '15px', borderRadius: '15px' }}
            >
              Add new Product
              <CreateProduct update={false}/>
            </button>
          </div>

        </div>

        <div>
          <InfiniteScrollProducts username={username} />
        </div>
      </article>

    );
  }

  render() {
    const { username } = this.state.user;
    const { loading, mobileView, isNormalUser } = this.state;
    return (
      <div>
        <SideDrawer user={this.state.user} location={this.props.location}/>


        {
            mobileView

              ? (
                <div className="nav-margin admin-bg">
                  <Tabs tabsData={[{ tabHeading: 'Edit Products', components: this.editLinkComponent.bind(this) }, { tabHeading: 'Preview', components: this.previewComponent.bind(this) }]} />
                </div>
              )
              : (
                <div className="admin-bg nav-margin flex ">
                  {
                    this.editLinkComponent(username)
                  }
                  {
                    this.previewComponent()
                  }

                </div>
              )
          }

        {
          loading ? <Loader /> : null
        }
        {
          isNormalUser
            ? <DisabledDiv message="Purchase a Pro pack to unlock this feature" /> : null
        }
      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  shopDetails: state.ProductsPageReducers.shopDetails,
});

const mapDispatchToProps = (dispatch) => ({
  setProducts: (products) => dispatch({ type: GET_PRODUCTS, payload: products }),
  getShopDetails: (user) => dispatch({ type: GET_SHOP_DETAILS, payload: agent.ProductsPage.getShopDetails(user) }),
  setShopDetails: (address) => dispatch({ type: GET_SHOP_DETAILS, payload: address }),
  getHeaderInfo: (user) => dispatch({ type: GET_HEADER_INFO, payload: agent.AdminPage.getHeaderInfo(user) }),
  getBackgroundStyle: (user) => dispatch({ type: GET_BG_STYLE, payload: agent.AdminPage.getBackground(user) }),
  getLinks: () => dispatch({ type: GET_LINKS, payload: agent.AdminPage.getLinks() }),
  getUserDetails: (user) => dispatch({ type: GET_USER_DETAILS, payload: agent.Auth.getUserDetails(user) }),

  // getCategories: (user) => dispatch({ type: GET_CATEGORIES, payload: agent.ProductsPage.getCategories(user) }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
